        margin-bottom: 30px;
    }
}
.blog-entry .text {
    width: 100%;
    margin: 0 auto;
}
.blog-entry .text .heading {
    font-size: 20px;
    margin-bottom: 16px;
    font-weight: 500;
}
.blog-entry .text .heading a {
    color: #000000;
